@import url(https://fonts.googleapis.com/css2?family=Encode+Sans+SC:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@200;300&family=Ubuntu:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.ant-layout .site-layout {
    background-image: url(/portfolio/static/media/Profile.7d462f94.jpg);
    background-size: cover;
    box-sizing: border-box;
  }

 
  .logo{

    background-image: url(/portfolio/static/media/logo.d4f908f7.jpg);
    height: 90px;
    width: 200px;
  }

  .site-layout .site-layout-background {
    background: rgba(255, 255, 255, 0.863);
  }
    [data-theme="dark"] .site-layout ,.site-layout-background {
      background: #141414;
    }



    .ant-layout-sider-children,.ant-menu-item{
    background:#141414;
}

.ant-menu-inline{
    background:#141414;

}
.ant-menu-dark{
  background:#141414 !important
}
.ant-menu-root{
    background:#141414 !important
}

.ant-menu-item-selected,.ant-menu-item-only-child{
  background:#141414 !important
}


.fade-appear {
  opacity: 0;
  -webkit-animation-duration: .5s;
          animation-duration: .5s;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
  -webkit-animation-timing-function: cubic-bezier(0.55, 0, 0.55, 0.2);
          animation-timing-function: cubic-bezier(0.55, 0, 0.55, 0.2);
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
}

.fade-leave {
  -webkit-animation-duration: .5s;
          animation-duration: .5s;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
  -webkit-animation-timing-function: cubic-bezier(0.55, 0, 0.55, 0.2);
          animation-timing-function: cubic-bezier(0.55, 0, 0.55, 0.2);
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
}

h1{
  font-size: 10em;
  padding: 2.5em;
  color: #fff !important;


}
h1 , h2 , h3 ,h4 {
  font-family: 'Encode Sans SC', sans-serif;
}

p{
  font-family: 'Ubuntu', sans-serif;
  font-size: 1em;
}
